.about {
    background-color: $secondary-background-color;
    padding: 60px 0 20px;
}
@media screen and (min-width: 768px) {
  .about {
    padding: 60px 0;
  }
}
@media screen and (min-width: 1200px) {
  .about {
    background-image: url(../images/about/desktop/about-bgmilk-des-x1.png);
    background-position: 50% 77%;
    background-repeat: no-repeat;
    background-size: 1200px;
    padding-bottom: 30px;
  }
}
@media screen and (min-width: 1200px) and (min-device-pixel-ratio: 2),
  screen and (min-width: 1200px) and (min-resolution: 192dpi),
  screen and (min-width: 1200px) and (min-resolution: 2x) {
  .about {
    background-image: url(../images/about/desktop/about-bgmilk-des-x2.png);
  }
}
.about__header {
  color: $secondary-title-color;
  text-align: center;
  margin: 0 0 15px;
  font-family: Titan One, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.14;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__header {
    font-size: 26px;
    line-height: 1.15;
  }
}
@media screen and (min-width: 1200px) {
  .about__header {
    font-size: 30px;
    line-height: 1.15;
  }
}
.about__title {
  max-width: 200px;
  text-align: center;
  color: $accent-color;
  align-items: center;
  margin: 0 auto 32px;
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14;
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__title {
    max-width: 429px;
    margin: 0 auto 40px;
    font-size: 48px;
    line-height: 1.15;
  }
}
@media screen and (min-width: 1200px) {
  .about__title {
    max-width: 534px;
    margin: 0 auto 60px;
    font-size: 58px;
    line-height: 1.15;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__box {
    justify-content: center;
    display: flex;
  }
}
@media screen and (min-width: 1200px) {
  .about__box {
    display: flex;
  }
}

.about__cow {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    @media screen and (min-width: 1200px) {
      margin-bottom: 0;
    }
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__img {
    margin-bottom: 0;
    margin-right: 32px;
  }
}
@media screen and (min-width: 1200px) {
  .about__img {
    margin-bottom: 0;
    margin-right: 99px;
  }
}
.about__text {
  z-index: 2;
  color: $primary-text-color;
  padding-right: 60px;
  margin: 0 0 40px;
  font-family: Dm Sans, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
  position: relative;
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__text {
    width: 334px;
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.9;
  }
}
@media screen and (min-width: 1200px) {
  .about__text {
    width: 463px;
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 1.76;
  }
}
.about__text--color {
  z-index: 1;
  color: $secondary-text-color;
  width: 100%;
  margin: 0;
  padding-right: 60px;
  font-family: Dm Sans, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
  position: relative;
}
@media screen and (max-width: 767.98px) {
  .about__text--color:before {
    content: "";
    width: 100%;
    height: 340px;
    z-index: -1;
    pointer-events: none;
    background-image: url(../images/about/mobile/about-bgmilk-mob-x1.png);
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    top: -60px;
    left: 0;
  }
}
@media screen and (min-width: 550px) and (max-width: 767px) {
  .about__text--color:before {
    top: -80px;
  }
}
@media screen and (max-width: 767.98px) and (min-device-pixel-ratio: 2),
  screen and (max-width: 767.98px) and (min-resolution: 192dpi),
  screen and (max-width: 767.98px) and (min-resolution: 2x) {
  .about__text--color:before {
    background-image: url(../images/about/mobile/about-bgmilk-mob-x2.png);
  }
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__text--color {
    width: 334px;
    font-size: 14px;
    line-height: 1.76;
  }
}
@media screen and (min-width: 1200px) {
  .about__text--color {
    width: 463px;
    font-size: 14px;
    line-height: 1.94;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__thumb {
    margin-bottom: 0;
  }
}
@media screen and (min-width: 1200px) {
  .about__thumb {
    margin-bottom: 0;
    padding-top: 90px;
  }
}
.about__button {
  color: $accent-color;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  align-items: center;
  margin-top: 20px;
  padding: 12px 37px;
  font-family: Dm Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.3;
  transition: background-color 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
}
@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .about__button {
    margin-top: 40px;
    font-size: 16px;
    line-height: 1.3;
  }
}
@media screen and (min-width: 1200px) {
  .about__button {
    margin-top: 40px;
    font-size: 16px;
    line-height: 1.3;
  }
}
.about__button:hover {
  background-color: #ecebeb;
}
.about__logo {
  margin-bottom: -3px;
  margin-left: 14px;
}
img {
  display: block;
}


