.contacts {
  padding-bottom: 40px;
  background-image: linear-gradient(
    to bottom,
    $primary-background-color 23%,
    $secondary-background-color 77%
  );
  @media screen and (min-width: $desktop) {
    padding-bottom: 60px;
  }
  @media screen and (min-width: $tablet) and (max-width: $tablet-max) {
    background-image: linear-gradient(
      to bottom,
      $primary-background-color 50%,
      $secondary-background-color 50%
    );
  }
  & .container {
    @media screen and (min-width: $tablet) and (max-width: $tablet-max) {
      & {
        padding-left: 16px;
        padding-right: 16px;
      }
    }
  }
  &__list {
    @media screen and (min-width: $tablet) {
      & {
        display: flex;
        flex-wrap: wrap;
        margin: -16px;
      }
    }
  }
  &__branch {
    min-width: auto;
    min-height: 412px;
    background-color: #fff;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;

    @media screen and (max-width: $mobile-max) {
      margin: 0 auto;
      max-width: 400px;
    }

    @media screen and (min-width: 768px) {
      & {
        margin: 16px;
        width: calc((100% - 3 * 32px) / 3);
      }
    }
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__btn-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 32px;
    @media screen and (min-width: $tablet) {
      flex-direction: row;
      margin-top: 60px;
      justify-content: center;
    }
  }
  &__btn-list > li:not(:last-child) {
    margin-bottom: 10px;
    @media screen and (min-width: $tablet) {
      & {
        margin-bottom: 0;
        margin-right: 15px;
      }
    }
  }
  &__btn .button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 161px;
    height: 44px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
    color: $primary-inverse-text-color;
    background-color: $accent-color;
    transform: rotateX(0);
    transition: background-color $default-transition-time
        $default-transition-function,
      transform $default-transition-time $default-transition-function;
    @media screen and (min-width: $tablet) {
      width: auto;
      padding: 0 31px;
    }
  }
  &__btn:hover .button,
  &__btn:focus .button {
    transform: rotateX(180deg);
    background-color: $secondary-title-color;
  }
  &__btn-text {
    transform: rotateX(0);
    transition: transform $default-transition-time $default-transition-function;
  }
  &__btn:hover &__btn-text,
  &__btn:focus &__btn-text {
    transform: rotateX(180deg);
  }
  &__btn:nth-child(2) .button {
    color: $accent-color;
    background-color: $primary-inverse-text-color;
    transition: background-color $default-transition-time
        $default-transition-function,
      transform $default-transition-time $default-transition-function;
    @media screen and (min-width: $tablet) {
      width: auto;
      padding: 0 39px;
    }
  }
  &__btn:nth-child(2):hover .button,
  &__btn:nth-child(2):focus .button {
    transform: rotateX(180deg);
    background-color: $secondary-title-color;
  }
  &__icon {
    fill: currentColor;
    margin-left: 8px;
    @media screen and (min-width: $tablet) {
      margin-left: 13px;
    }
  }
}
.branch {
  padding: 40px 32px;
  @media screen and (max-width: $mobile-max) {
    font-size: 12px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 16px;
  }
  &__location {
    margin-bottom: 8px;
  }
  &__type {
    display: flex;
    padding: 0 24px;
    background-color: #f0d1a5;
    height: 16px;
    width: max-content;
    border-radius: 4px;
    font-weight: 500;
    line-height: 1.8;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $primary-inverse-text-color;
    @media screen and (min-width: $tablet) {
      height: 28px;
    }
    &--color {
      background-color: #c2e297;
    }
  }
  &__content {
    margin-top: 24px;
    &:not(:last-child)::after {
      display: block;
      content: '';
      height: 1px;
      width: 100%;
      background-color: #e1e1e1;
    }
  }

  &__content .link {
    display: block;
    line-height: 1.5;
    color: $secondary-text-color;
    transition: color $default-transition-time $default-transition-function;
    &:hover,
    &:focus {
      color: $accent-color;
    }
  }
  &__text {
    margin-bottom: 24px;
    color: $secondary-text-color;
  }
  &__weekdays {
    margin-bottom: 8px;
  }
  &__days {
    line-height: 1.8;
    color: $primary-text-color;
  }
  &__time {
    line-height: 1.8;
    color: $secondary-title-color;
  }
  &__weekends {
    margin-bottom: 24px;
  }
}
