// Chertok code above

.products__meta {
    position: relative;
    overflow: hidden;
}



.products__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    // padding: 40px 20px 0 20px;

    // display: flex;
    // flex-direction: column;
    // justify-content: space-between;

    transform: translateX(-100%);

    // background-color: teal;

    transition: transform $default-transition-time $default-transition-function;

    &--start {
        transform: translateX(0);
    }

}



.products__button {
    position: absolute;
    bottom: calc(var(--card-padding-bottom) - 40px); // padding - button-height 
    left: 50%;
    transform-origin: center;
    transform: translateX(-50%) rotate(0);

    transition: transform $default-transition-time $default-transition-function, background-color $default-transition-time $default-transition-function;

    &.return-arrow {
        transform: translateX(-50%) rotate(-180deg);
        transition: transform $default-transition-time $default-transition-function, background-color $default-transition-time $default-transition-function;
    }

}